.page {
  font-family: "Roboto", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  color: #060b35;

  --main-color: #00acdc;

  &__body {
    margin: 0;
  }

  &__main {
    display: grid;
    grid-template-columns: repeat(4, 200px);
    justify-content: center;
    padding-top: 50px;

    row-gap: 46px;
    column-gap: 48px;
  }
}
